.jumbotron-home {
    margin: 0;
    background-color: #554c45;
    height: 100vh;
    position: relative;
    z-index: -1;
}

.jumbotron-home h1 {
    margin: 0;
    text-align: center;
    color: #d3d3cb;
    font-family: 'Palanquin Dark', sans-serif;
}

.jumbotron-home .logo-img {
    margin-left: auto;
    margin-right: auto;
    max-width: 25%;
    max-height: 100%;
}

.jumbotron-home .jumbo-title {
    position: absolute;
    width: 100%;
    height: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-line-pack: center;
        align-content: center;
    -ms-flex-pack: center;
        justify-content: center;
}


